import center from '@/views/center'
	import qiye from '@/views/modules/qiye/list'
	import news from '@/views/modules/news/list'
	import gangweileixing from '@/views/modules/gangweileixing/list'
	import zhaopinxinxi from '@/views/modules/zhaopinxinxi/list'
	import xuesheng from '@/views/modules/xuesheng/list'
	import mianshijieguo from '@/views/modules/mianshijieguo/list'
